
.db_nav {
  height: 43px;
  line-height: 43px;
  border-bottom: 2px solid #3ba94d;
  background-color: #fff;
}
.db_nav ul {
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  display: -o-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.db_nav ul li {
  display: inline-block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  vertical-align: middle;
  text-align: center;
}
.db_nav ul li a.active {
  color: #3ba94d;
}
.db_nav ul li a {
  color: #4a4a4a;
  font-size: 15px;
}
.db_nav.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}



