
.db_hot_shop .db_shop_item {
  margin-bottom: 30px;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.db_hot_shop .shop_info {
  padding: 25px 10px;
}
.db_hot_shop .shop_top {
  height: 30px;
  line-height: 30px;
}
.db_hot_shop .shop_img {
  display: block;
  width: 20px;
  height: 30px;
  line-height: 23px;
  float: left;
  margin-right: 10px;
}
.db_hot_shop .shop_img img {
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}
.db_hot_shop .shop_name {
  display: block;
  width: 70%;
  color: #4a4a4a;
}
.db_hot_shop .shop_intrduction {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #9b9b9b;
  font-size: 15px;
}
.db_hot_shop .shop_entry {
  text-align: right;
  padding: 0 10px 10px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.db_hot_shop .shop_entry a {
  color: #4a4a4a;
  font-size: 15px;
}
.loading {
  text-align: center;
  color: #999;
  font-size: 12px;
}
.none {
  text-align: center;
  color: #999;
  font-size: 12px;
}



